import { records } from '../helpers/api';

const initialState = {
    page: 1,
    pageLength: 100,
    items: [],
    itemCount: 0,
    statusFilter: "",
    searchTarget: "",
    searchFilter: "",
    allColumns: [
        { title: "ID", value: "id", active: true },
        { title: "Title", value: "title", active: true },
        { title: "Author info", value: "author", active: true },
        { title: "URL", value: "url", active: true },
        { title: "Comment", value: "comment", active: true },
        { title: "Status", value: "status", active: true },
        { title: "Edited by", value: "editedBy", active: true },
    ]
};

const getters = {
};

const mutations = {
    SET_PAGE(state, payload) {
        state.page = payload;
    },
    SET_PAGE_LENGTH(state, payload) {
        state.pageLength = payload;
    },
    SET_ITEMS(state, payload) {
        state.items = payload;
    },
    SET_ITEM_COUNT(state, payload) {
        state.itemCount = payload;
    },
    SET_STATUS_FILTER(state, payload) {
        state.statusFilter = payload;
    },
    SET_SEARCH_TARGET(state, payload) {
        state.searchTarget = payload;
    },
    SET_SEARCH_FILTER(state, payload) {
        state.searchFilter = payload;
    },
    SET_ALL_COLUMNS(state, payload) {
        state.allColumns = payload;
    }
};

const actions = {
    async fetchItems({ commit, state }, where) {
        const { page, pageLength, statusFilter } = state;

        const items = await records.index({
            offset: (page - 1) * pageLength,
            limit: pageLength,
            ...where,
            ...(statusFilter !== "" && { status: statusFilter })
        });

        commit('SET_ITEMS', items.data.records.map(({ id, title, author, url, comment, status, editedBy }) => ({ id, title, author, url, comment, status, editedBy })));
        commit('SET_ITEM_COUNT', items.data.count);
    },
    async setStatusFilter({ commit, dispatch }, payload) {
        commit('SET_STATUS_FILTER', payload);
        commit('SET_PAGE', 1);
        await dispatch("fetchItems");
    },
    async setSearchTarget({ commit, state, dispatch }, payload) {
        commit('SET_SEARCH_TARGET', payload);
        if (state.searchFilter.length) {
            commit('SET_PAGE', 1);
            await dispatch("fetchItems");
        }
    },
    toggleColumn({ commit, state }, {active, ...rest}) {
        const columns = [...state.allColumns];
        const colIndex = columns.findIndex(column => column.value === rest.value);
        columns[colIndex] = {...rest, active: !active};
        commit('SET_ALL_COLUMNS', columns);
    },
    // async setSortingField({ commit, state, dispatch }, payload) {
    //     // todo
    // }
};

export default {
    namespaced: true,
    state: initialState,
    getters,
    mutations,
    actions,
};
