<template>
  <section id="data-tab">
    <section class="table-filters">
      <div class="table-filters__filter">
        <label>Status:</label>
        <v-select
          @input="(item) => setStatusFilter(item.value)"
          :options="statusOptions"
          :value="statusOptions.find(status => status.value === statusFilter).label"
          :clearable="false"
          :searchable="false"
        ></v-select>
      </div>
      <div class="table-filters__filter">
        <label>Search:</label>
        <input type="text" placeholder="search words" />
        <label>from:</label>
        <v-select
          @input="(field) => setSearchTarget(field.value)"
          :options="searchFields"
          :value="searchFields.find(field => field.value === searchTarget).label"
          :clearable="false"
          :searchable="false"
        ></v-select>
      </div>
      <div class="table-filters__filter">
        <label>Show columns:</label>
        <span
          v-for="col in allColumns"
          :key="col.value"
          :class="{'table-filters__tag--active': col.active }"
          class="table-filters__tag"
          @click.prevent="() => toggleColumn(col)"
        >{{ col.title }}</span>
      </div>
    </section>
    <hot-table
      :data="items"
      :colHeaders="colHeaders"
      :readOnly="true"
      :width="'100%'"
      :style="style"
      :colWidths="colWidths"
      :manualColumnResize="true"
      :manualRowResize="true"
      :beforeColumnSort="beforeColumnSort"
      :columnSorting="columnSorting"
      licenseKey="non-commercial-and-evaluation"
      ref="hotTableComponent"
    ></hot-table>
    <section class="table-pagination">
      <div class="table-pagination__filter">
        <label>Rows per page:</label>
        <v-select
          @input="(field) => setSearchTarget(field.value)"
          :options="searchFields"
          :value="searchFields.find(field => field.value === searchTarget).label"
          :clearable="false"
          :searchable="false"
        ></v-select>
      </div>
    </section>
  </section>
</template>
<script>
import HotTable from "@handsontable/vue";
import { mapActions, mapState } from "vuex";
import vSelect from "vue-select";

export default {
  name: "DataTab",
  components: {
    HotTable,
    vSelect,
  },
  data: function () {
    return {
      columns: [
        { data: "id" },
        { data: "title" },
        { data: "author" },
        { data: "url" },
        { data: "comment" },
        { data: "status" },
        { data: "editedBy" },
      ],
      colHeaders: [
        "ID",
        "Title",
        "Author info",
        "URL",
        "Comment",
        "Status",
        "Edited by",
      ],
      colWidths: [50, 500, 400, 300, 300, 100, 100],
      columnSorting: {
        sortEmptyCells: true,
        indicator: true, 
        headerAction: true,
        compareFunctionFactory: function () {
          return function () {
            return 0;
          };
        },
      },
      statusOptions: [
        { label: "All", value: "" },
        { label: "Unset", value: "null" },
        { label: "Uncertain", value: "uncertain" },
        { label: "Excluded", value: "excluded" },
        { label: "Included", value: "included" },
      ],
      searchFields: [
        { label: "All fields", value: "" },
        { label: "ID", value: "id" },
        { label: "Title", value: "title" },
        { label: "Author info", value: "author" },
        { label: "Comment", value: "comment" },
      ],
    };
  },
  computed: {
    ...mapState({
      searchTarget: (state) => state.data.searchTarget,
      items: (state) => state.data.items,
      pageLength: (state) => state.data.pageLength,
      statusFilter: (state) => state.data.statusFilter,
      allColumns: (state) => state.data.allColumns,
    }),
    style() {
      return `width: 100%; height: 100%; overflow: hidden; font-size: 12px; font-weight: 600; border: 0 solid #c0c0c0; border-width: 1px 0 2px 0;`;
    },
  },
  async mounted() {
    await this.fetchItems();
    this.$refs.hotTableComponent.hotInstance.loadData(this.items);
  },
  watch: {
    items: function () {
      this.$refs.hotTableComponent.hotInstance.loadData(this.items);
    },
  },
  methods: {
    ...mapActions({
      fetchItems: "data/fetchItems",
      setStatusFilter: "data/setStatusFilter",
      setSearchTarget: "data/setSearchTarget",
      toggleColumn: "data/toggleColumn",
      setSortingField: "data/setSortingField",
    }),
    beforeColumnSort: async function (
      currentSortConfig,
      destinationSortConfigs
    ) {
      const columnSortPlugin = this.$refs.hotTableComponent.hotInstance.getPlugin(
        "columnSorting"
      );
      columnSortPlugin.setSortConfig(destinationSortConfigs);
      console.log(destinationSortConfigs);
      // await this.setSortingField(destinationSortConfigs);
      this.$refs.hotTableComponent.hotInstance.loadData(this.items);
      return false;
    },
  },
};
</script>
<style lang="scss" scoped>
#data-tab {
  position: relative;
  min-height: 100%;
  height: 80vh;
}

.table-filters,
.table-pagination {
  display: flex;
  flex-wrap: wrap;

  &__filter {
    display: flex;
    padding: 5px;

    label {
      font-size: 12px;
      margin-right: 10px;
      line-height: 30px;
    }

    input[type="text"] {
      border: 1px solid rgba(60, 60, 60, 0.26);
      margin-right: 5px;
      padding: 0 5px;

      &:focus {
        box-shadow: none;
        outline: none;
      }
    }

    .v-select {
      flex: 1;
      min-width: 150px;
    }

    + .table-filters__filter {
      margin-left: 20px;
    }
  }

  &__tag {
    font-size: 12px;
    margin-right: 5px;
    border: 1px solid #eaeaea;
    background-color: #eaeaea;
    height: 20px;
    padding: 3px 6px;
    border-radius: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    user-select: none;

    &--active {
      background-color: #d7d7d7;
      border: 1px solid #c0c0c0;
    }
  }
}
</style>