<template>
  <section id="activity-history">
    <h4>Activity history</h4>
    <ul class="item-list">
      <li
        v-for="item in logs"
        @click="() => setIdFilter(item.Record.id)"
        :key="item.id"
        class="item"
      >
        <small class="item-timestamp">{{ formatTimestamp(item.createdAt) }}</small>
        <small>
          id:
          <b>{{ item.Record.id }}</b> </small
        >&nbsp;
        <span class="item-title">{{ truncate(item.description) }}</span>
      </li>
    </ul>
    <ul class="pagination">
      <li
        @click="movePage(1)"
        class="pagination-item"
        :class="[historyPage <= 1 && 'pagination-item--disabled']"
      > First</li>
      <li
        @click="movePage(historyPage - 1)"
        class="pagination-item"
        :class="[historyPage <= 1 && 'pagination-item--disabled']"
      > Prev</li>
      <li
        @click="movePage(historyPage + 1)"
        class="pagination-item"
        :class="[historyPage >= historyCount / historyPageLength && 'pagination-item--disabled']"
      >Next </li>
      <li
        @click="movePage(Math.ceil(historyCount / historyPageLength))"
        class="pagination-item"
        :class="[historyPage >= historyCount / historyPageLength && 'pagination-item--disabled']"
      >Last </li>
    </ul>
  </section>
</template>
<script>
import { mapActions, mapState } from "vuex";
import { format as formatDate } from "date-fns";

export default {
  name: "ActivityHistory",
  computed: {
    ...mapState(["logs", "historyPageLength", "historyCount", "historyPage"]),
  },
  mounted() {
    this.fetchHistory();
  },
  methods: {
    ...mapActions(["fetchHistory", "setHistoryPage","setIdFilter"]),
    truncate(str) {
      return str.length > 20 ? `${str.substring(0, 20)}...` : str;
    },
    formatTimestamp(str) {
      return formatDate(new Date(str), "dd.MM.yyyy HH:mm:ss ");
    },
    movePage(to) {
      if (to > 0) {
        this.setHistoryPage(to);
      }
    }
  },
};
</script>
