import NProgress from 'vue-nprogress';

export const keyCodes = {
    ARROW_LEFT: 37,
    ARROW_RIGHT: 39
};

export const nprogress = new NProgress({ speed: 100, showSpinner: true })

export const beforeColumnSort = async function (currentSortConfig, destinationSortConfigs) {
    const columnSortPlugin = this.getPlugin("columnSorting");
    columnSortPlugin.setSortConfig(destinationSortConfigs);
    // await this.setSortingField(destinationSortConfigs);
    this.loadData(this.items);
    return false;
  }